/* src/index.css */

/* Reset & Global Styles */
html, body, #root {
  margin: 0;
  padding: 0;
  font-family: 'Pretendard Variable', sans-serif;
  height: 100%;
  width: 100%;
  overflow: hidden;
}

/* Countdown “pop” animation */
@keyframes pop {
  0% {
    transform: scale(0.5);
    opacity: 0;
  }
  50% {
    transform: scale(1.2);
    opacity: 1;
  }
  100% {
    transform: scale(1);
    opacity: 1;
  }
}
.animate-pop {
  animation: pop 0.6s ease-out forwards;
}

/* Progress bar smooth shrink animation */
@keyframes progress {
  0% {
    width: 100%;
  }
  100% {
    width: 0%;
  }
}
.animate-progress {
  animation: progress 5s linear forwards;
}

/* Fade-in animation for overlays and screens */
@keyframes fade-in {
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}
.animate-fade-in {
  animation: fade-in 0.5s ease-out forwards;
}
